@import "../../common/mixins";

@include block('tiles-table') {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0;

  @include element('header') {
    user-select: none;
    display: flex;
    padding: 15px 25px 5px 20px;
    justify-content: space-between;
    align-content: center;
    width: 100%;
  }

  @include element('controlls') {
    display: grid;
    align-content: center;
  }

  @include element('table') {
    overflow: auto;
    height: 300px;
    table {
      width: 100%;

    }

    width: 100%;
    table-layout: fixed;
  }
  

  @include element('table-row') {
    @include modifier('selected') {
      td {
        // color: white !important;
        background-color: rgba(191, 204, 214, 0.8);
      }
    }
  }

  @include element('loader') {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
  }

  @include element('star-icon') {
    margin-bottom: 3px;
    svg {
      fill: #F7AD00 !important;
    }
  }
}