@import "../common/colors";
@import "../common/mixins";

.map-tile {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  
  
  @include element('top') {
    display: flex;
  }

  @include element('table') {
    flex: 1 1 auto;
  }

  @include element('info') {
    flex-basis: 25%
  }

  @include element('bottom') {
    display: flex;
    width: 100%;
    height: 100%;
    flex: 1;
    margin-top: 10px;
    
    & > :first-child {
      margin-right: 10px; 
    }
  }
  
}